import React from 'react'
import PropTypes from 'prop-types'
import styled from 'styled-components'
import pxToRem from '../utils/px_to_rem'
import { colorWhite, colorGreen } from '../styles/colors'

const StyledCTA = styled.a`
  color: ${colorWhite};
  font-size: ${pxToRem(24)};
  font-family: druk-bold;
  text-transform: uppercase;
  text-decoration: none;
  &:hover {
    color: ${colorGreen};
  }
`
const label = 'Apply for a grant'

const ApplyForGrantCTA = (props) => {
  const { grantApplicationLink } = props
  return (
    <StyledCTA href={grantApplicationLink} data-tracking={'link-click:mainNav_applyForAGrant'}>{label}</StyledCTA>
  )
}

ApplyForGrantCTA.propTypes = {
  grantApplicationLink: PropTypes.string.isRequired,
}

export default ApplyForGrantCTA
