import React from 'react'
import PropTypes from 'prop-types'

import { StyledWrapper, StyledButton } from './styled'

const EditLocation = (props) => {
  const {
    postcodeValue,
    handleEditLocation,
  } = props
  return (
    <StyledWrapper>
      {`Your Suburb: ${postcodeValue} `}
      <StyledButton data-tracking="btn-click:editLocation" type="button" onClick={handleEditLocation}>Edit location</StyledButton>
    </StyledWrapper>
  )
}

EditLocation.propTypes = {
  postcodeValue: PropTypes.string.isRequired,
  handleEditLocation: PropTypes.func.isRequired,
}

export default EditLocation
