import React from 'react'
import PropTypes from 'prop-types'

import { StyledDisclaimerWrapper, StyledDisclaimer } from './styled'

import { colorNavy } from '../../styles/colors'

const FiguresDisclaimer = (props) => {
  const { copy, color, sourceName, sourceLink } = props
  return (
    <StyledDisclaimerWrapper>
      <StyledDisclaimer
        color={color}>
        {copy}&nbsp;
        Source:&nbsp;
        <a href={sourceLink} target="_blank" className="typography__link--external">{sourceName}</a>
      </StyledDisclaimer>
    </StyledDisclaimerWrapper>
  )
}

FiguresDisclaimer.propTypes = {
  copy: PropTypes.string.isRequired,
  color: PropTypes.string,
  sourceName: PropTypes.string.isRequired,
  sourceLink: PropTypes.string.isRequired,
}

FiguresDisclaimer.defaultProps = {
  color: colorNavy,
}

export default FiguresDisclaimer
