import React from 'react'
import posed from 'react-pose'
import {
  Desktop,
  TabletRetinaDown,
} from '../../utils/media'


import { StyledWrapper, StyledMessageWrapper } from './styled'
import { IgniteYourAmbitionText } from '../../styled/typography'

const MainMessagePose = posed.div({
  init: {
    x: '20%',
    // opacity: 0,
  },
  enter: {
    x: '-200%',
    // opacity: 1,
    transition: {
      x: {

        duration: 5000,
      },
    },
  },
})
const IgniteYourAmbition = (props) => {
  const { isVisible } = props
  return (
    <>
      <Desktop>
        <StyledWrapper>
          <MainMessagePose
            initialPose="init"
            pose={isVisible ? 'enter' : 'init'}
          >
            <StyledMessageWrapper>
              <IgniteYourAmbitionText className="message" />
            </StyledMessageWrapper>
          </MainMessagePose>
        </StyledWrapper>
      </Desktop>

      <TabletRetinaDown>
        <StyledWrapper>
          <StyledMessageWrapper>
            <IgniteYourAmbitionText className="message" />
          </StyledMessageWrapper>
        </StyledWrapper>
      </TabletRetinaDown>
    </>
  )
}

export default IgniteYourAmbition
